// vmthisvmvuex
// https://uviewui.com/js/http.html#%E4%BD%95%E8%B0%93%E8%AF%B7%E6%B1%82%E6%8B%A6%E6%88%AA%EF%BC%9F
const install = (Vue, vm) => {
	// params
	// user	
	const sendCode = (phone) => vm.$u.post('api/user/code?phone=' + phone)
	
	const login = (params) => vm.$u.post('api/user/login', params)
	
	const getUserInfo = () => vm.$u.post('api/user/info')
	
	const logout = () => vm.$u.get('api/user/logout')
	
	// vendor
	const getVendorBriefInfo = (vendorId) => vm.$u.get(`api/vendor/briefInfo/${vendorId}`)
	
	const getCommentList = (params) => vm.$u.post('api/vendor/commentList', params)
	
	const updateVendorInfo = (params) => vm.$u.post('api/vendor/updateVendorInfo', params)
	
	const getCategoryList = (vendorId) => vm.$u.get('api/vendor/category/' + vendorId)
	
	// order
	const queryByVendorId = (params) => vm.$u.post('api/order/queryByVendorId', params)
	
	const checkQrCode = (orderId) => vm.$u.post('api/order/writeOff/' + orderId)
	
	// Product
	const updateProduct = (params) => vm.$u.post('api/vendor/updateProduct', params)
	
	const addProduct = (params) => vm.$u.post('api/vendor/addProduct', params)
	
	// vm.$u.api(vmthisthis.$u.api)
	vm.$u.api = {sendCode, login, getUserInfo, getVendorBriefInfo, logout, queryByVendorId, checkQrCode, updateVendorInfo, updateProduct, 
	getCategoryList, addProduct, }
}

export default {
	install
}