// vmvuethisvuextoken
const install = (Vue, vm) => {
	// 
	Vue.prototype.$u.http.setConfig({
		baseUrl: 'http://59.79.1.46:80',
		loadingText: '~', // loading
		// jsonJSON.parse()
		dataType: 'json',
		showLoading: true, // loading
		loadingTime: 800, // ms
		originalData: false, // 
		loadingMask: true, // loading
		// 
		header: {
			'content-type': 'application/json;charset=UTF-8;'
		}
	})

	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// token
		// vuextokenuViewvuex
		config.header.TOKEN = vm.vuex_merchant_token
		config.header["Access-Control-Allow-Origin"]="*";
		// configreturn	
		return config
		// returnfalse
		// if(config.url == '/user/rest') return false // 
	}

	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		const {code, data, message} = res
		if (code === 200) {
			return data
		} else {
			uni.showToast({
				title: message,
				icon: "error"
			})
			setTimeout(()=>{
				uni.hideToast()
			}, 800)
			return false
		}
	}
}

export default {
	install
}
